
import { reqCarList, reqDeleteCarById, reqUpdateCheckedById } from '@/api/index'


export default {
    namespaced: true,
    state: {
        cartList: []
    },
    actions: {
        async getCartList({ commit }) {
            let result = await reqCarList()
            if (result.code == 200) {
                commit("GETCARTLIST", result.data)
            }
        },
        //删除购物车选中产品
        async deleteCartListBySkuId({ commit }, skuId) {
            let result = await reqDeleteCarById(skuId)
            if (result.code == 200) {
                return 'ok'
            } else {
                return Promise.reject(new Error('faile'))
            }
        },
        //修改购物车产品的选中状态
        async updateCheckedById({ commit }, { skuId, isChecked }) {
            let result = await reqUpdateCheckedById(skuId, isChecked)
            if (result.code == 200) {
                return 'ok'
            } else {
                return Promise.reject(new Error('faile'))
            }
        },
        //删除选中的所有产品
        deleteAllChecked({ dispatch, getters }) {
            //获取购物车中全部的产品
            let PromiseAll = []
            getters.cartList.cartInfoList.forEach(item => {
                //注意，因为是在store里action调用另外的action，就不需要再加名字了：shopcart/deleteCartListBySkuId
                let promise = item.isChecked == 1 ? dispatch("deleteCartListBySkuId", item.skuId) : ""
                PromiseAll.push(promise)
            })
            return Promise.all(PromiseAll)
        },
        //修改全部产品选中状态
        updateAllCartIsChecked({ dispatch, getters  }, isChecked) {
            let PromiseAll = []
            getters.cartList.cartInfoList.forEach(item => {
                let promise = item.isChecked == isChecked ? "" : dispatch("updateCheckedById", { skuId: item.skuId, isChecked, })
                PromiseAll.push(promise)
            })
            return Promise.all(PromiseAll)
        }
    },
    mutations: {
        GETCARTLIST(state, cartList) {
            state.cartList = cartList
        }


    },
    getters: {
        cartList(state) {
            return state.cartList[0] || {}
        },

    },
}
