<template>
  <div>
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread 面包屑-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <!-- 分类的面包屑 -->
            <li class="with-x" v-if="searchParams.categoryName">
              {{ searchParams.categoryName }}
              <i @click="removeCategoryName">x</i>
            </li>
            <!-- 关键字的面包屑 -->
            <li class="with-x" v-if="searchParams.keyword">
              {{ searchParams.keyword }}
              <i @click="removeKeyword">x</i>
            </li>
            <!-- 品牌信息的面包屑 -->
            <li class="with-x" v-if="searchParams.trademark">
              {{ searchParams.trademark.split(":")[1] }}
              <i @click="removeTrademark">x</i>
            </li>
            <!-- 平台售卖的面包屑 注意不能用v-if-->
            <li
              class="with-x"
              v-for="(attrProp, index) in searchParams.props"
              :key="index"
            >
              {{ attrProp.split(":")[1] }}
              <i @click="removeAttrProp(index)">x</i>
            </li>
          </ul>
        </div>

        <!--selector-->
        <SearchSelector @trademarkInfo="trademarkInfo" @attrInfo="attrInfo" />

        <!--details-->
        <div class="details clearfix">
          <!-- 排序 -->
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li :class="{ active: isShowOne }" @click="changeOrder('1')">
                  <a href="#"
                    >综合<span
                      v-show="isShowOne"
                      class="iconfont"
                      :class="{ 'icon-up': upOrDown, 'icon-down': !upOrDown }"
                    ></span
                  ></a>
                </li>
                <li :class="{ active: isShowTwo }" @click="changeOrder('2')">
                  <a href="#"
                    >价格<span
                      v-show="isShowTwo"
                      class="iconfont"
                      :class="{ 'icon-up': upOrDown, 'icon-down': !upOrDown }"
                    ></span
                  ></a>
                </li>
              </ul>
            </div>
          </div>
          <!-- 商品列表 -->
          <div class="goods-list">
            <ul class="yui3-g">
              <li class="yui3-u-1-5" v-for="goods in goodsList" :key="goods.id">
                <div class="list-wrap">
                  <div class="p-img">
                    <router-link :to="`/detail/${goods.id}`"
                      ><img v-lazy="goods.defaultImg"
                    /></router-link>
                  </div>
                  <div class="price">
                    <strong>
                      <em>¥</em>
                      <i>{{ goods.price }}</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a
                      target="_blank"
                      href="item.html"
                      title="促销信息，下单即赠送三个月CIBN视频会员卡！【小米电视新品4A 58 火爆预约中】"
                      >{{ goods.title }}</a
                    >
                  </div>
                  <div class="commit">
                    <i class="command">已有<span>2000</span>人评价</i>
                  </div>
                  <div class="operate">
                    <a
                      href="success-cart.html"
                      target="_blank"
                      class="sui-btn btn-bordered btn-danger"
                      >加入购物车</a
                    >
                    <a href="javascript:void(0);" class="sui-btn btn-bordered"
                      >收藏</a
                    >
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!-- 分页器、 -->
          <Pagination
            :pageNo="searchParams.pageNo"
            :pageSize="searchParams.pageSize"
            :total="searchList.total"
            :continues="5"
            @getPageNo="getPageNo"
          ></Pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from "./SearchSelector/SearchSelector";
import { mapGetters, mapState } from "vuex";
import LoginVue from "../Login/";
export default {
  name: "search",

  components: {
    SearchSelector,
  },
  data() {
    return {
      searchParams: {
        category1Id: "",
        category2Id: "",
        category3Id: "",
        categoryName: "",
        //关键字
        keyword: "",
        //排序  初始状态默认综合且降序
        order: "1:desc",
        //分页器，代表当前是第几页
        pageNo: 1,
        //代表的是每一页展示数据个数
        pageSize: 10,
        //平台售卖属性操作带的参数
        props: [],
        //品牌
        trademark: "",
      },
    };
  },
  //当组件挂载完毕之前执行一次
  beforeMount() {
    Object.assign(this.searchParams, this.$route.query, this.$route.params);
  },
  mounted() {
    this.getData();
  },
  computed: {
    ...mapGetters("search", ["goodsList"]),
    ...mapState("search", ["searchList"]),
    isShowOne() {
      return this.searchParams.order.includes("1");
    },
    isShowTwo() {
      return this.searchParams.order.includes("2");
    },
    upOrDown() {
      return this.searchParams.order.includes("asc");
    },
  },
  methods: {
    //向服务器发请求获取search模块数据
    getData() {
      this.$store.dispatch("search/getSearchList", this.searchParams);
    },
    //删除面包屑标签名字
    removeCategoryName() {
      //置为undefined 属性就不会带给服务器
      this.searchParams.categoryName = undefined;
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      //因为路由跳转的时候会自动调用一次getData，所以下面这句就不需要了
      // this.getData();
      //地址栏也需要修改 进行路由跳转
      //有个小问题 空对象总为真 if判断没啥意义
      // if(this.$route.params){
      this.$router.push({ name: "search", params: this.$route.params });
      // }
    },
    removeKeyword() {
      //注意 keyword是Header组件里的
      this.searchParams.keyword = undefined;
      this.$bus.$emit("clear");
      this.$router.push({ name: "search", query: this.$route.query });
    },
    //自定义事件回调
    trademarkInfo(trademark) {
      //整理品牌参数
      this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`;

      this.getData();
    },
    removeTrademark() {
      this.searchParams.trademark = "";
      this.getData();
    },
    //收集平台属性的回调
    attrInfo(attr, attrValue) {
      let props = `${attr.attrId}:${attrValue}:${attr.attrName}`;
      //数组去重
      // if(!this.searchParams.props.includes(props)){
      //   this.searchParams.props.push(props);
      // }
      this.searchParams.props.push(props);
      this.searchParams.props = [...new Set(this.searchParams.props)];
      this.getData();
    },
    removeAttrProp(index) {
      this.searchParams.props.splice(index, 1);
      this.getData();
    },
    //排序
    changeOrder(flag) {
      //flag代表用户点击的是综合还是价格
      if (flag == 1) {
        //判断是否是已经选择状态决定要不要改排序
        if (this.isShowOne == true) {
          if (this.searchParams.order.split(":")[1] === "desc") {
            this.searchParams.order = this.searchParams.order.replace(
              "desc",
              "asc"
            );
          } else {
            this.searchParams.order = this.searchParams.order.replace(
              "asc",
              "desc"
            );
          }
          //写的繁琐了，可以把order里的1:desc用split拆成三个变量
          //然后用模板字符串和三元表达式判断来进行拼接
        } else {
          this.searchParams.order = this.searchParams.order.replace("2", "1");
        }
      } else if (flag == 2) {
        if (this.isShowTwo == true) {
          if (this.searchParams.order.split(":")[1] === "desc") {
            this.searchParams.order = this.searchParams.order.replace(
              "desc",
              "asc"
            );
          } else {
            this.searchParams.order = this.searchParams.order.replace(
              "asc",
              "desc"
            );
          }
        } else {
          this.searchParams.order = this.searchParams.order.replace("1", "2");
        }
      }
      this.getData();
    },
    //获取当前第几页
    getPageNo(pageNo) {
      this.searchParams.pageNo = pageNo;
      this.getData();
    },
  },
  watch: {
    $route(newValue, oldValue) {
      //监听路由的信息是否发生变化，如果发生变化，再次发起请求
      //每一次请求完毕，应该把相应的1、2、3级分类的id置空的，让他接受下一次的相应1、2、3
      //再次发请求之前整理带给服务器参数
      Object.assign(this.searchParams, this.$route.query, this.$route.params);
      //分类名字与关键字不用清理：因为每一次路由发生变化的时候，都会给他赋予新的数据
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      //再次发起ajax请求
      this.getData();
    },
  },
};
</script>

