//当前这个模块：API进行管理
import requests from './request'

import mockRequests from './mockAjax'
//三级联动接口


export const reqCategoryList = () => requests({
    url: '/product/getBaseCategoryList',
    method: 'get'
})

//获取banner(轮播图接口)
export const reqGetBannerList = () => mockRequests({
    url: '/banner',
    method: 'get'
})

//获取floor
export const reqFloorList = () => mockRequests.get('/floor')

//获取搜索模块数据
export const reqGetSearchInfo = (params) => requests({
    url: "/list",
    method: "POST",
    data: params
})

//获取产品详情
export const reqGoodsInfo = (skuId) => requests({
    url: `/item/${skuId}`,
    method: 'GET',
})

//将产品添加到购物车中（获取更新某一个产品的个数）
export const reqAddOrUpdateShopCart = (skuId, skuNum) => requests({
    url: `/cart/addToCart/${skuId}/${skuNum}`,
    method: 'POST'
})

//获取购物车列表数据接口
export const reqCarList = () => requests({
    url: '/cart/cartList',
    method: 'GET'
})

//删除选中的购物车产品
export const reqDeleteCarById = (skuId) => requests({
    url: `/cart/deleteCart/${skuId}`,
    method: 'delete'
})

//修改商品的选中状态
export const reqUpdateCheckedById = (skuId, isChecked) => requests({
    url: `/cart/checkCart/${skuId}/${isChecked}`,
    method: 'get'
})

//获取验证码
export const reqGetCode = (phone) => requests({
    url: `/user/passport/sendCode/${phone}`,
    method: 'get'
})

//注册
export const reqUserRegister = (data) => requests({
    url: '/user/passport/register',
    data,
    method: 'post'
})

//登录
export const reqUserLogin = (data) => requests({
    url: 'user/passport/login',
    data,
    method: 'post'
})

//利用token获取用户信息  把token加请求头的操作在请求拦截器里完成
export const reqUserInfo = () => requests({
    url: '/user/passport/auth/getUserInfo',
    method: 'get'
})

//退出登录
export const reqLogout = () => requests.get('/user/passport/logout')

//获取用户地址信息（结算时候用的）
export const reqAddressInfo = () => requests.get('/user/userAddress/auth/findUserAddressList')

//获取商品清单
export const reqOrderInfo = () => requests.get('order/auth/trade')

//提交订单的接口
export const reqSubmitOrder = (tradeNo, data) => requests({
    url: `/order/auth/submitOrder?tradeNo=${tradeNo}`,
    method: 'POST',
    data,
})

//获取订单支付信息
export const reqPayInfo = (orderId) => requests.get(`/payment/weixin/createNative/${orderId}`)

//获取支付订单状态
export const reqPayStatus = (orderId) => requests.get(`/payment/weixin/queryPayStatus/${orderId}`)

//获取个人中心数据
export const reqMyOrderList = (page,limit) => requests.get(`/order/auth/${page}/${limit}`)

//