//配置路由的地方
import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import store from '@/store/index'

//引入路由组件
//home用懒加载的形式引入  在下面
import Home from '@/pages/Home/Home.vue'
import Search from '@/pages/Search/Search.vue'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
import Detail from '@/pages/Detail'
import AddCartSuccess from '@/pages/AddCartSuccess'
import ShopCart from '@/pages/ShopCart'
import Trade from '@/pages/Trade'
import Pay from '@/pages/Pay'
import PaySuccess from '@/pages/PaySuccess'
import Center from '@/pages/Center'
import MyOrder from '@/pages/Center/myOrder'
import GroupOrder from '@/pages/Center/groupOrder'


let originPush = VueRouter.prototype.push;
let originReplace = VueRouter.prototype.replace;

//重写push|replace ，解决NavigationDuplicated问题
VueRouter.prototype.push = function (location, resolve, reject) {
    if (resolve && reject) {
        originPush.call(this, location, resolve, reject)
    } else {
        originPush.call(this, location, () => { }, () => { })
    }
}
//上面的简写   originPush.call(this, location).catch(err=>err)
//重写replace
VueRouter.prototype.replace = function (location, resolve, reject) {
    originReplace.call(this, location).catch(err => err)
}

//配置路由
let router = new VueRouter({
    routes: [
        {
            name: "center",
            path: "/center",
            component: Center,
            meta: { show: true },
            children: [
                {
                    name: 'myorder',
                    path: 'myorder',
                    component: MyOrder,
                },
                {
                    name: 'grouporder',
                    path: 'grouporder',
                    component: GroupOrder,
                },
                {
                    path: '/center',
                    redirect: '/center/myorder'
                }
            ]
        },
        {
            name: "paysuccess",
            path: "/paysuccess",
            component: PaySuccess,
            meta: { show: true },
        },
        {
            name: "pay",
            path: "/pay",
            component: Pay,
            meta: { show: true },
        },
        {
            name: "trade",
            path: "/trade",
            component: Trade,
            meta: { show: true },
            //路由独享守卫
            beforeEnter(to,from,next){
                //只有从shopcart来才允许访问trade
                //之所以还要判断 '/trade'  因为router里做过重定向,在trade页面刷新会跳到根目录
                if(from.path == "/shopcart" || '/trade'){
                    next()
                } else {
                    //如果url改变，则重置到from  从哪来回哪去
                    next(false)
                }
            }
        },
        {
            name: "shopcart",
            path: "/shopcart",
            component: ShopCart,
            meta: { show: true },
        },
        {
            name: "addcartsuccess",
            path: "/addcartsuccess",
            component: AddCartSuccess,
            meta: { show: true },
        },
        {
            name: "detail",
            path: "/detail/:skuid",
            component: Detail,
            meta: { show: true },
        },
        {
            name: "home",
            path: "/home",
            //路由懒加载 返回的是一个promise
            component: () => import('@/pages/Home/Home.vue'),
            //show 显示底下footer是否展示
            meta: { show: true },
        },
        {
            name: "search",
            //占位后面加问号 params参数就可以 可传可不传
            path: "/search/:keyword?",
            component: Search,
            meta: { show: true },
            // props: {a:1,b:2}      
        },
        {
            name: "Login",
            path: "/login",
            component: Login,
        },
        {
            name: "Register",
            path: "/register",
            component: Register,
        },
        //重定向，在项目跑起来的时候，访问/，立马让他定向到首页
        {
            path: '*',
            redirect: 'home'
        }
    ],
    // 滚动行为
    scrollBehavior(to, from, savedPosition) {
        return { y: 0 }
    }
})

//全局守卫  前置守卫
router.beforeEach(async (to, from, next) => {
    //未登录时禁止去的页面
    // let forbitRouter = ['/trade', '/pay', '/center']

    //localstorage具有时效性，也可以判定store里的token是不是undefined
    // let token = localStorage.getItem("TOKEN")
    let token = store.state.user.token;
    let username = store.state.user.userInfo.name;
    //用户登录
    if (token) {
        //登录了以后无法去login
        if (to.path == '/login') {
            next('/home')
        } else {
            //已经登陆了,访问的是非登录与注册
            //登录了且拥有用户信息放行
            if (username) {
                next();
            } else {
                //登陆了且没有用户信息
                //在路由跳转之前获取用户信息且放行
                try {
                    await store.dispatch('user/getUserInfo');
                    next();
                } catch (error) {
                    //token失效从新登录
                    await store.dispatch('user/userLogout');
                    next('/login')
                }
            }

        }
    } else {
        //未登录不能去交易相关,支付相关,个人中心的页面
        //也可以加个元数据meta  没携带token的都不能进
        let toPath = to.path
        if (toPath.indexOf('/trade') !== -1 || toPath.indexOf('/pay') !== -1 || toPath.indexOf('/center') !== -1) {
            //未登录时想去而没去成的页面的路径存储于路由中
            next('/login?redirect='+toPath)
        } else {
            next()
        }

    }
})


export default router