<script>
//导入生成二维码的包
import QRCode from "qrcode";
export default {
  name: "Pay",
  data() {
    return {
      payInfo: {},
      timer: null,
      code: "",
    };
  },
  computed: {
    orderId() {
      return this.$route.query.orderId;
    },
  },
  methods: {
    //获取支付信息
    async getPayInfo() {
      let result = await this.$API.reqPayInfo(this.orderId);
      if (result.code == 200) {
        this.payInfo = result.data;
      }
    },
    //弹出遮罩层（微信二维码）
    async open() {
      //生成二维码地址
      let url = await QRCode.toDataURL(this.payInfo.codeUrl);
      this.$alert(`<img src=${url} />`, "请你微信支付", {
        dangerouslyUseHTMLString: true,
        center: true,
        //是否显示取消按钮
        showCancelButton: true,
        //取消按钮的文本内容
        cancelButtonText: "支付遇到问题",
        //确定按钮的文本
        confirmButtonText: "已支付",
        //右上角的×去掉
        // showClose: false,
        //关闭弹出框的配置
        beforeClose: (type, instance, done) => {
          //type: 区分取消 确定按钮
          //instance: 当前组件实例
          //done: 关闭弹出框的方法
          if (type == "cancel") {
            alert("请练习管理员");
            clearInterval(this.timer);
            this.timer = null;
            done();
          } else {
            //点击的 支付成功 按钮
            //判断是否真的支付了
            //方便测试 先把if注释了
            // if (this.code == 200) {    
              clearInterval(this.timer);
              this.timer = null;   
              done()
              this.$router.push("/paysuccess");
            // }
          }
        },
      });
      //需要判断支付是否成功 所以要长时间向服务器询问
      if (!this.timer) {
        this.timer = setInterval(async () => {
          //发请求获取用户支付状态
          let result = await this.$API.reqPayStatus(this.orderId);
          //支付成功
          if (result.code == 200) {
            clearInterval(this.timer);
            this.timer = null;
            //保存支付成功的code 供 点击 支付成功时用
            this.code = result.code;
            //关闭弹出层
            this.$msgbox.close();
            //跳转到支付成功界面
            this.$router.push("/paysuccess");
          }
        }, 1000);
      }
    },
  },
  mounted() {
    this.getPayInfo();
  },
};
</script>

